import {Controller, useForm} from "react-hook-form";
import {useState} from "react";
import s from './Form.module.css'
import {
    Autocomplete, Box, Button,
    FormControl,
    FormControlLabel,
    FormLabel,
    Radio,
    RadioGroup,
    TextField
} from "@mui/material";
import {AdapterDayjs} from "@mui/x-date-pickers/AdapterDayjs";
import {DatePicker, LocalizationProvider} from "@mui/x-date-pickers";
import {countries} from "./countries/countries";
import useFormPersist from "react-hook-form-persist";



 type FormData = {
    FirstName: string
    LastName: string
    eMail: string
    message: string
    radio: string
    phone:string
    date:string
    country:string
    address:string
};

export function Form() {
    let [success, setSuccess] = useState(false)
    let [country, setCountry] = useState<any>('1')


    const {register, handleSubmit,watch,setValue, reset,control, formState: {errors, isValid}} = useForm<FormData>(
        {
            mode: "onBlur",
        }
    );

    useFormPersist("storageKey", {
        watch,
        setValue,
        storage: window.localStorage, // default window.sessionStorage
        exclude: ['baz']
    });
    const onSubmit = (data: FormData) => {
        console.log(data)
        reset()
        setSuccess(true)

        setTimeout(() => {
            setSuccess(false)
        }, 3000)

    }

    const random = () => {
        let value = Math.random()
        if (value >= 0.5) {
            alert(' ')
        } else {
            alert('-   ,  ')
        }
    }
    return <div>
        <form className={s.form} onSubmit={handleSubmit(onSubmit)}>
            <TextField style={{paddingTop:'20px'}} size={"small"} variant={"standard"} {...register("FirstName", {
                setValueAs: value => value.trim(),
                required: true,
                minLength:{value:2,message:' 2 '},
                pattern:/^(?=.{1,40}$)[--]+(?:[-' ][--]+)*$/
            })}

                       className={s.input}
                       placeholder={''}/>
            <div style={{
                height: 30,
                color: 'red',
            }}>{errors?.FirstName && <>{errors?.FirstName?.message || '   '}</>}</div>
            <TextField size={"small"} variant={"standard"} {...register("LastName", {
                setValueAs: value => value.trim(),
                minLength: {value: 2, message: ' 2 '},
                pattern:/^(?=.{1,40}$)[--]+(?:[-' ][--]+)*$/
            })}
                       className={s.input}
                       placeholder={''}/>
            <div style={{
                height: 30,
                color: 'red',
            }}>{errors?.LastName && <>{errors?.LastName?.message || '   '}</>}</div>

            <div>
                <FormControl>
                    <FormLabel id="demo-controlled-radio-buttons-group"></FormLabel>
                    <RadioGroup
                        aria-labelledby="demo-controlled-radio-buttons-group"
                        name="controlled-radio-buttons-group"

                    >
                        <FormControlLabel {...register('radio', {required: true})} value="female" control={<Radio/>}
                                          label=""/>
                        <FormControlLabel {...register('radio', {required: true})} value="male" control={<Radio/>}
                                          label=""/>
                    </RadioGroup>
                </FormControl>
            </div>
            <TextField size={"small"} variant={"standard"} {...register("phone", {
                setValueAs: value => value.trim(),
                required:false,
                minLength: {value: 3, message: ' 3 '},
                pattern:/^(\s*)?(\+)?([- _():=+]?\d[- _():=+]?){10,14}(\s*)?$/
            })}
                       className={s.input}
                       placeholder={' '}/>
            <div style={{
                height: 30,
                color: 'red',
            }}>{errors?.phone && <>{errors?.phone?.message || '  '}</>}</div>
            <TextField size={"small"} variant={"standard"} {...register("eMail", {
                setValueAs: value => value.trim(),
                required:'E-mail ',
                minLength: {value: 3, message: ' 3 '},
                pattern:/^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
            })}
                       className={s.input}
                       placeholder={'E-mail'}/>
            <div style={{
                height: 30,
                color: 'red',
            }}>{errors?.eMail && <>{errors?.eMail?.message || ' E-mail'}</>}</div>


            <Controller
                control={control}
                name={'date'}
                render={({ field: {value,onChange}}) => <LocalizationProvider  dateAdapter={AdapterDayjs} >
                        <DatePicker   inputFormat={'DD.MM.YYYY'}
                                      value={value}
                                      label={' '}
                                      onChange={onChange}
                                      renderInput={(params ) => <TextField color={"secondary"}  {...params}   />}
                        />
                    </LocalizationProvider>
                }
            />
            <Autocomplete
                id="country-select-demo"
                sx={{ width: 300 ,paddingTop:'20px'}}
                options={countries}
               onChange={(event, value, reason, details)=>{setCountry(value?.label!==null?value?.label:'')}}
                autoHighlight
                getOptionLabel={(option) => option.label}
                renderOption={(props, option) => (
                    <Box
                        component="li" sx={{ '& > img': { mr: 2, flexShrink: 0 } }} {...props}>
                        <img
                            loading="lazy"
                            width="20"
                            src={`https://flagcdn.com/w20/${option.code.toLowerCase()}.png`}
                            srcSet={`https://flagcdn.com/w40/${option.code.toLowerCase()}.png 2x`}
                            alt=""
                        />
                        {option.label} ({option.code}) +{option.phone}
                    </Box >
                )}
                renderInput={(params) => (
                    <TextField
                        {...params}
                        label=" "
                        {...register("country",{setValueAs:value => value})}
                        inputProps={{
                            ...params.inputProps,
                            autoComplete: 'new-password',
                        }}
                    />
                )}
            />

            {country?.length>1&&<TextField {...register("address")}placeholder={''} style={{paddingTop:'20px'}}/>}
            {success &&
                <div className={s.success}><h3 className={s.sendMessage}>The message was sent successfully</h3></div>}
            <div>
                <Button style={{marginTop:'20px'}} type={"submit"} variant={"contained"} title={'Send'} disabled={!isValid} onClick={random}></Button>
            </div>

        </form>
    </div>
}